<template>
    <AppBar :title="title" :centerTitle="true" back='category' />
    <!-- exam section -->
    <div class="mx-auto md:container md:px-24">
    <MainTitle :parent="parent" :child="child" />
    <Listening1 :parent="parent" :child="child" :year="year" :type="type" mondai="mondai1" />
    <Listening2 :parent="parent" :child="child" :year="year" :type="type" mondai="mondai2" />
    <Listening3 :parent="parent" :child="child" :year="year" :type="type" mondai="mondai3" />
    <Listening4 :parent="parent" :child="child" :year="year" :type="type" mondai="mondai4" />
    <Finished :parent="parent" :child="child" />
    
    <!-- exam section -->

    <!-- result section -->
    <div v-if="parent=='showresult'" class="pt-14 mb-18">
      <Result1 :year="year" :type="type" mondai="mondai1"/>
      <Result2 :year="year" :type="type" mondai="mondai2"/>
      <Result3 :year="year" :type="type" mondai="mondai3"/>
      <Result4 :year="year" :type="type" mondai="mondai4"/>
    </div>
    <!-- score section -->
    <Result :parent="parent" :year="year" :type="type" />
    </div>
</template>
<script>
// @ is an alias to /src
import AppBar from '@/components/AppBar.vue';

// exam section
import MainTitle from '@/components/choukai/MainTitle.vue';
import Listening1 from '@/components/choukai/Listening1.vue';
import Listening2 from '@/components/choukai/Listening2.vue';
import Listening3 from '@/components/choukai/Listening3.vue';
import Listening4 from '@/components/choukai/Listening4.vue';
import Finished from '@/components/choukai/Finished.vue';

// result section
import Result1 from '@/components/choukai/result/Result1.vue';
import Result2 from '@/components/choukai/result/Result2.vue';
import Result3 from '@/components/choukai/result/Result3.vue';
import Result4 from '@/components/choukai/result/Result4.vue';
// Total Score
import Result from '@/components/Result.vue';

var audio = new Audio();
export default {
  name: 'ChouKaiView',
  data: function() {
    return {
      parentData: '',
      childData: '',
      index: 0,
    };
  },
  components: {
    AppBar,
    MainTitle,
    Listening1,
    Listening2,
    Listening3,
    Listening4,
    Finished,
    Result1,
    Result2,
    Result3,
    Result4,
    Result,
  },
  mounted() {
    this.start();
  },
  methods: {
    setScore: function() {
      this.$store.dispatch('storeInit');
      let score = this.$store.state.scores;
      let choukai = this.$store.state[this.year].choukai;
      let mark=0;
      let count=0;     
      
      for (let a=0;a<choukai.mondai1.questions.length;a++){
        count++;
        let question = choukai.mondai1.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark++;
          }
          this.$store.state[this.year].choukai.mondai1.questions[a].pick='';
        }
      }
      for (let a=0;a<choukai.mondai2.questions.length;a++){
        count++;
        let question = choukai.mondai2.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark++;
          }
          this.$store.state[this.year].choukai.mondai2.questions[a].pick='';
        }
      }
      for (let a=0;a<choukai.mondai3.questions.length;a++){
        count++;
        let question = choukai.mondai3.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark++;
          }
          this.$store.state[this.year].choukai.mondai3.questions[a].pick='';
        }
      }
      for (let a=0;a<choukai.mondai4.questions.length;a++){
        count++;
        let question = choukai.mondai4.questions[a];
        if(question.pick){
          if(question.pick==question.true){
            mark++;
          }
          this.$store.state[this.year].choukai.mondai4.questions[a].pick='';
        }
      }

      score[this.year].listening = Math.round( (mark*60)/count );

      // score[this.year].vocab_knowledge = 19;
      // score[this.year].reading = 30;
      
      this.$store.dispatch('setScore',score);
      this.setState('score','');
    },
    callBack: function() {
      if(audio.duration > 0 && !audio.paused){
        audio.pause();
      }
      // de-select
      let choukai = this.$store.state[this.year].choukai;  
      
      for (let a=0;a<choukai.mondai1.questions.length;a++){
        let question = choukai.mondai1.questions[a];
        if(question.pick){
          this.$store.state[this.year].choukai.mondai1.questions[a].pick='';
        }
      }
      for (let a=0;a<choukai.mondai2.questions.length;a++){
        let question = choukai.mondai2.questions[a];
        if(question.pick){
          this.$store.state[this.year].choukai.mondai2.questions[a].pick='';
        }
      }
      for (let a=0;a<choukai.mondai3.questions.length;a++){
        let question = choukai.mondai3.questions[a];
        if(question.pick){
          this.$store.state[this.year].choukai.mondai3.questions[a].pick='';
        }
      }
      for (let a=0;a<choukai.mondai4.questions.length;a++){
        let question = choukai.mondai4.questions[a];
        if(question.pick){
          this.$store.state[this.year].choukai.mondai4.questions[a].pick='';
        }
      }
    },
    setState: function(parent,child) {
      this.parentData=parent;
      this.childData=child;
    },
    start: async function(){
      let schedule = this.$store.state[this.year][this.type].schedule[this.index];
      let parent = schedule.parent;
      let child = schedule.child;
      let delay = schedule.delay;
      let url = schedule.url;
      let arrayLen = this.$store.state[this.year][this.type].schedule.length;
      this.setState(parent,child);
      
      // // Testing Purpose    >>>>>>>
      // await this.sleep(3000);
      // if(this.index<arrayLen-1){
      //   this.index++;
      //   this.start();
      // }else{
      //   this.setState('finished','');
      // }
      // // Testing Purpose    >>>>>>>

      // play sound         <<<<<<<
      const songUrl = require(`@/assets/${this.year}/${url}`);
      audio.autoplay = true;
      audio.src = songUrl;
      audio.play();
      
      audio.onended = async () => {
        await this.sleep(delay);
        if(this.index<arrayLen-1){
          this.index++;
          this.start();
        }else{
          this.setState('finished','');
        }
      };
      // play sound         <<<<<<<

    },
    sleep: function(ms) {
        return new Promise((resolve) => {
            setTimeout(resolve, ms);
        });
    }
  },
  computed: {
    title: function() {
      this.$store.dispatch('storeInit');
      return this.parentData!='score'?this.$store.state.currentTitle + " 聴解":'score';
    },
    year: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentYear;
    },
    type: function() {
      this.$store.dispatch('storeInit');
      return this.$store.state.currentType;
    },
    parent: function() {
      return this.parentData;
    },
    child: function() {
      return this.childData;
    }
  }
}
</script>